/*==========  Mobile First Method  ==========*/




@media only screen and (min-width : 480px) {
    .equip-section {
        background-image:url(../img/common/equip-bg-1200.jpg);
    }
}


@media only screen and (min-width : 768px) {
    .divided-text-bg {
        background-image:url(../img/geometry/red.png);
        transform: rotate(180deg);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        transform: rotate(180deg);
    }
    .follow-block {
        background-image:url(../img/geometry/red.png);
        background-size: 100% 100%;
    }

    .accord-bg {
      display: block;
      position: absolute;
      top: -27px;
      bottom: -16px;
      left: 50px;
      right: 0;
      transform: rotate(-180deg);
      background-image:url(../img/geometry/red.png);
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: 100% 100%;
      z-index: -1;
