
/* ==================== common styles ======================= */

html {
  overflow-x: hidden;
}

body {
  position: relative;
  min-width: 320px;
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  font-family: $preg;
  font-size: 18px;
  color: #fff;
  line-height: 1.2;
  background-color: #3F3F3F;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.overlay {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0, .8);
  z-index: 10;
  &.transparent {
    background: transparent;
  }
}

.flex {
  display: -webkit-flex;
  display: flex;
  flex-wrap: wrap;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}

a,
button {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  text-decoration: none;
  outline: none;
  border: none;
  background: none;
  cursor: pointer;

}

img {
  outline: none;
  border: none;
  max-width: 100%;
}


input,
input:focus,
textarea {
  outline: none;
}

::-webkit-input-placeholder {
  font-family: $preg;
  font-size: 23px;
  color: #81cc00;
}

::-moz-placeholder {
  font-family: $preg;
  font-size: 23px;
  color: #81cc00;
}

:-ms-input-placeholder {
  font-family: $preg;
  font-size: 23px;
  color: #81cc00;
}

::placeholder {
  font-family: $preg;
  font-size: 23px;
  color: #81cc00;
}

b,
strong {
  font-family: $cpbold;
}

section {
  position: relative;
}

.wrapper {
  width: 96%;
  max-width: 1920px;
  margin: 0 auto;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: rgba(0,0,0,.7);
  color: #fff;
  //padding: 15px;
  z-index: 9;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  display: block;
  min-width: 78px;
  width: 78px;
  margin-right: 5px;

}


.header-nav-list {
  white-space: nowrap;
  height: 46px;
  line-height: 46px;
  li {
    display: inline-block;

  }
  a {
    text-transform: uppercase;
    font-family: $preg;
    font-size: 18px;
    color: #fff;
    padding: 0 12px;

    transition: all .4s;
    &:hover,
    &.active {
      color: #FBA01E;
    }
  }

}


.header-social {
  white-space: nowrap;
  li {
    display: inline-block;
    max-width: 39px;
    max-height: 39px;
    margin-right: 8px;
    vertical-align: middle;
    &:last-child {
      margin-right: 0;
    }
  }
  a {
    display: block;
    img {
      filter: grayscale(100%);
      transition: all .4s;
    }
    &:hover {
      img {
        filter: grayscale(0);
      }
    }
  }
}

.call-btn {
  cursor: pointer;
}

/////////////

.btn-menu {
  display: none;
  position: fixed;
  top: 9px;
  right: 10px;
  width: 40px;
  height: 40px;
  background: $red;
  border: 1px solid #fff;
  border-radius: 5px;
  cursor: pointer;
  z-index: 6;
}

.btn-menu span {
  display: block;
  background: #fff;
  position: absolute;
  top: 18px;
  left: 8px;
  right: 8px;
  height: 3px;
  border: none;
  border-radius: 2px;
}

.btn-menu span:before, .btn-menu span:after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background: #fff;
  border-radius: 2px;
}

.btn-menu span:before {
  top: -10px;
}

.btn-menu span:after {
  top: 10px;
}

.mob-cont {
  position: fixed;
  width: 320px;
  background-color: #252525;
  top: 0;
  bottom: 0;
  right: -350px;
  overflow-y: auto;
  padding: 20px 15px 20px 30px;
  z-index: 11;
  color: #0a0a0a;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  &:after {
    content: "";
    display: block;
    width: 120px;
    height: 120px;
    position: absolute;
    bottom: 40px;
    right: 10px;
    background-image:url(../img/common/silh1.png);
    background-size: 100% 100%;
    //transform: rotate(-8deg);
    filter: grayscale(100%);
    
  }
}

.mob-cont.active {
  right: 0;
}

.mob-cont li {
  display: block;
  margin-bottom: 20px;
  white-space: nowrap;
}

.mob-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 35px;
}


.mob-nav {
  margin-bottom: 20px;
}
.mob-nav a {
  position: relative;
  display: inline-block;
  font-family: $pmed;
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;

}


.mob-nav a.active:after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: $red;
  position: absolute;
  bottom: 0;
  left: 0;
}

.mob-logo {
  max-width: 120px;
}


.mob-close {
  display: block;
  font-family: $cpbold;
  font-size: 36px;
  color: $red;
  cursor: pointer;
}

.mob-social {
  margin-bottom: 15px;
  li {
    display: inline-block;
    max-width: 34px;
    margin-right: 10px;
    &:last-child {
      margin-right: 0;
    }
  }
  img {
    filter: grayscale(100%);
  }
}

