/***/
.slogan-section {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image:url(../img/common/dots.png);
    background-repeat: repeat;
    z-index: -1;
  }
}

  
.bg-img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: -3;
}

[id^="fig-"] {
  display: none;
}

.arenas-block {
  display: flex;
  justify-content: center;
  position: relative;
  padding: 50px 0 5px;
  background-color: rgba(0,0,0, .5);
  //z-index: 1;
}

.arena {
  flex: 0 1 320px;
  padding: 10px 20px;
  position: relative;
  color: $green;
  text-align: center;
  >span,
  >a {
    display: block;
    font-size: 16px;
    width: 100%;
    margin-bottom: 2px;
  }
  a {
    transition: all .4s;
    &:hover {
     color: $or;
    }
  }
  &:after {
    content: "";
    display: block;
    width: 1px;
    height: 82%;
    position: absolute;
    top: 9%;
    right: 0;
    background-color: $red;

  }
  &:last-child {
    &:after {
      display: none;
    }
  }
}

.appoint-btn {
  display: block;
  width: 285px;
  max-width: 285px;
  height: 42px;
  line-height: 32px;
  font-family: $pmed;
  font-size: 18px;
  border: 5px solid $green;
  margin-top: 6px;
  text-transform: uppercase;
  color: #fff;
  margin: 8px auto 0;
  transition: all .4s;
  &:hover {
    color: $or;
  }
}

.white {
  color: #fff;
}

.main-slogan {
  position: relative;
  text-transform: uppercase;
  text-align: center;
  margin-top: 60px;
  margin-bottom: 80px;
  .slogan {
    font-family: $cpbold;
    font-size: 72px;
    letter-spacing: 1.2px;
    
    position: relative;
    padding-bottom: 10px;
    margin-bottom: 22px;
    &:after {
      content: "";
      display: block;
      width: 120px;
      height: 3px;
      background-color: $green;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .slogan-sub {
    font-family: $cpr;
    font-size: 30px;
    letter-spacing: 1px;
    strong {
      white-space: nowrap;
    }
  }
}
.main-logo {
  display: block;
  max-width: 500px;
  padding-left: 100px;
  margin: 0 auto 45px;
}


.scroll-i {
