}

.equip-block {
  position: relative;
  text-align: center;
}

.eq-red-line,
.feat-red-line {
  display: none;
}

.equip-list {
  position: relative;
  margin: 0 auto 84px;
  z-index: 1;
  li {
    display: inline-block;
    vertical-align: top;
    transition: all 1s cubic-bezier(0.53, 1.9, 0.35, 0.89);
    &:last-child {
      margin-right: 0;
    }
  }
}

.eq-img-cont {
  display: block;
  width: 157px;
  height: 157px;
  border-radius: 50%;
  border: 2px solid $green;
  overflow: hidden;
  margin: 0 auto 36px;
  transition: all .4s;
  &:hover {
    box-shadow: 0 0 10px 4px $green;
    .eq-thumb {
      transform: scale(1.05);
      filter: brightness(110%);
    }
  }
}

.equip-list li:nth-child(even) {
  .eq-name:before {
    background-color: $green;
  }
}



.eq-thumb {
  display: block;
  width: 100%;
  height: 100%;
  border: 4px solid #000;
  border-radius: 50%;
  z-index: -1;
  transition: all .4s;
}

.eq-name,
.feat-text {
  position: relative;
  font-family: $pmed;
  font-size: 18px;
  letter-spacing: .4px;
  text-transform: uppercase;
  max-width: 130px;
  text-align: center;
  margin: 0 auto;
  &:before {
    content: "";
    display: block;
    width: 50px;
    height: 4px;
    background-color: $red;
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.schemes-list {
  text-align: center;
  margin: 0 auto 32px;
