  border-radius: 50%;
  margin: 5px;
}

.feat-text {
  max-width: 200px;
  letter-spacing: .8px;
  
}


.accordion-block {
  position: relative;
  top: -150px;
  padding-top: 150px;
}



.accord-title {
  position: absolute;
  top: -97px;
  left: -62px;
  width: 100%;
  max-width: 350px;
  font-family: $cpbold;
  font-size: 24px;
  white-space: nowrap;
  color: #FFC300;
  text-align: center;
  padding: 63px 20px 86px 3px;
  background-image:url(../img/geometry/red.png);
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: -60px;
  z-index: 0;
}

.accord-outer {
  position: relative;
  max-width: 870px;
  min-height: 400px;
  padding-right: 100px;
  margin: 0 auto 30px;
  z-index: 5;
}

.accord-bg {
  display: none;
}

.accordion {
  position: absolute;
  top: 0;
  left: 28px;
  width: 800px;
  max-width: 100%;
  li {
    display: block;
  }
}

.accordion li:nth-child(6n+1) .accord-item-title {
  background-color: #F15A24;
}

.accordion li:nth-child(6n+2) .accord-item-title {
  background-color: #00D8FF;
}

.accordion li:nth-child(6n+3) .accord-item-title {
  background-color: #81CC00;
}

.accordion li:nth-child(6n+4) .accord-item-title {
  background-color: #FFC300;
}

.accordion li:nth-child(6n+5) .accord-item-title {
  background-color: #F15A24;
}

.accordion li:nth-child(6n+6) .accord-item-title {
  background-color: #009DFF;
}

.accord-item-title {
  font-family: $cpr;
  position: relative;
  height: 48px;
  line-height: 46px;
  font-size: 24px;
  white-space: nowrap;
  text-transform: uppercase;
  border: 1px solid #fff;
  padding: 0 10px 0 42px;
  cursor: pointer;
  transition: all .4s;

  &:before {
    left: 12px;
    top: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(0, 0, 0, 0);
    border-left-color: #fff;
    border-width: 10px;
    margin-top: -10px;
    border-left-width: 20px;
  }
  &:hover {
    font-size: 25px;
    filter: brightness(114%);
  }
}

.accord-text {
  display: none;
  padding: 40px 70px 30px 125px;
  font-size: 18px;
  color: #00680A;
  background-color: #B5E200;
  border-right: 1px solid #fff;
  border-left: 1px solid #fff;
  >div,
  p,
  span,
  ul,
  ol {
    margin-bottom: 17px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

