    }
  }
}

.together-heading {
  position: relative;
  font-family: $cpbold;
  font-size: 36px;
  line-height: 1;
  letter-spacing: 1.5px;
  
  color: $green;
  text-transform: uppercase;
  max-width: 780px;
  margin: 0 auto 45px;
  &:before,
