  &:after {
    width: 80px;
    height: 5px;
    background-color: $red;
    bottom: -24px;
  }
}

.prices-section {
  .wrapper {
    position: relative;
    top: -150px;
    padding-top: 150px;
    max-width: 960px;
    
  }
}



.prices-block {
  max-width: 960px;
  position: relative;
  background-color: #81CC00;
  margin-bottom: 55px;
  z-index: 1;
}

.prices-heading {
  position: absolute;
  top: -100px;
  left: 200px;
  width: 100%;
  max-width: 357px;
  height: 220px;
  font-family: $cpbold;
  font-size: 24px;
  text-align: center;
  padding-top: 50px;
  background-image:url(../img/geometry/green.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-bottom: -70px;
  z-index: -1;
}

.tables-cont {
  transition: all .4s;
}

.table-viewport {
  background-color: #81CC00;
  overflow: hidden;
}

.table-switch-cont {
  display: flex;
  justify-content: space-between;
  text-align: center;
  font-family: $preg;
  font-size: 26px;
  color: #00680A;
  text-transform: uppercase;
  background: linear-gradient(180deg, #81CC00, #019309);
}

.table-switch {
  flex: 1 1 50%;
  width: 50%;
