    left: 0;
    right: 0;
    background-color: rgba(0,0,0,.4);
  }
}

.slider-outer {
  opacity: 0;
  position: relative;
}

.js-big-slider {
  img {
    max-height: 100vh;
    margin: 0 auto;
    outline: none;
  }
}



.small-slider-cont {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.js-small-slider {
  width: 96vw !important;
  max-width: 1340px;
  img {
    outline: none;
    //height: 40px;
  }
}
.slick-slide {
  outline: none;
}
.js-small-slider .slick-slide {
  padding: 0 4px;
}

.js-small-slider img {
  cursor: pointer;
  height: 60px !important;
  border: 2px solid $green;
  margin: 0 auto;
  
}

.js-small-slider .slick-current img {
  border-color: #fff;
}

.arrows-cont {
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 86px;
  left: 50%;
  transform: translateX(-50%);
  width: 160px;
  

}

.slider-btn {
  display: block;
  width: 70px;
  height: 70px;
  background-color: rgba(0,0,0, .4);
  background-image:url(../img/common/arr.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 30px 19px;
  cursor: pointer;
  transition: all 1.2s;
}

.slider-prev {
  transform: rotate(90deg);
}

.slider-next {
  transform: rotate(-90deg);
}

.faq-section {
  padding: 180px 0 50px;
  
  .wrapper {
    position: relative;
    max-width: 960px;
    z-index: 2;
  }
  &:before,
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &:before {
    background-image:url(../img/common/together-bg-480.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-attachment: fixed;
    z-index: 0;
  }
  &:after {
    background-color: rgba(0,0,0, .3);
    z-index: 1;
  }
}

.faq-heading-cont {
  position: absolute;
  top: -100px;
  right: 0;
  width: 230px;
  height: 135px;
  line-height: 135px;
  font-family: $cpbold;
  font-size: 36px;
  color: #D4FC32;
  text-align: center;
  background-image:url(../img/geometry/g2.png);
  background-size: 100% 100%;
}

.faq-accordion {
  margin-bottom: 250px;
  li {
    display: block;
  }
}

.faq-accord-title {
  font-family: $cpr;
  font-size: 18px;
  text-transform: uppercase;
  color: #D4FC32;
  height: 60px;
  line-height: 59px;
  padding: 0 15px;
  border-bottom: 1px solid $red;
  cursor: pointer;
  transition: all .4s;
  &:hover,
  &.open {
    color: #fff;
  }
}

.answer-cont {
  display: none;
  
  font-family: $preg;
  font-size: 18px;
  line-height: 1.2;
  p {
    max-width: 555px;
    padding-left: 8px;
    margin-left: 15px;
    border-left: 1px solid #D4FC32;
    &:nth-child(1) {
      flex: 1 1 380px;
    }
    &:nth-child(2) {
      flex: 1 1 360px;
    }
  }
}

.answer-text {
  display: flex;
  justify-content: flex-end;
  
  padding: 30px 95px 30px 65px;
}

.faq-img {
  display: block;
  position: relative;
  width: 100px;
  height: 100px;
  margin: 0 auto;
